.card-list-wrapper {
  display: flex;
  flex-direction: column;
}
.card-list {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 30px;
}
.error-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.no-liked-message {
  font-size: 30px;
  color: red;
  display: flex;
  justify-content: center;
}
