.text-btn {
    display: flex;
    margin: 30px auto;
    padding: 5px 10px;
    font-size: 20px;
    background-color: transparent;
    border-radius: 5px;
    border: 1px solid rgb(184, 182, 182);
    cursor: pointer;
  }
  .text-btn:hover {
    background-color: rgb(238, 235, 235);
  }
